import React, { Component } from 'react';
import queryString from 'query-string';
import Button from '@material-ui/core/Button';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';
import { Link, withRouter } from 'react-router-dom';
import { withStyles } from '@material-ui/core/styles';
import Loader from 'react-loader-spinner';
import 'react-loader-spinner/dist/loader/css/react-spinner-loader.css';
import EmailAPI from '../../api/EmailAPI';
import { UserContext } from '../../contexts/UserContext';
import crossRedCircle from '../../assets/cross_red_circle.svg';
import checkGreenCircle from '../../assets/check_green_circle.svg';
import styles from '../../styles/validateEmail.css';

class ValidateEmail extends Component {
  static contextType = UserContext;

  state = {
    message: null,
    error: null,
    loaded: false,
  };

  componentDidMount() {
    const { token } = queryString.parse(this.props.location.search);
    if (!this.context.user.isValidated) {
      EmailAPI.validate(token)
        .then(() => {
          this.setState({
            message: 'Email has been validated.',
            error: null,
            loaded: true
          });
        })
        .catch(err => {
          this.setState({
            message: null,
            error:
              err.response.data.message ||
              'An error occurred while validating email.',
            loaded: true
          });
        });
    } else {
      this.setState({
        message: 'Email already validated.',
        error: null,
        loaded: true
      })
    }
  }

  componentWillUnmount() {
    this.context.getCurrentUser();
  }

  render() {
    const { classes } = this.props;
    const { message, error, loaded } = this.state;
    let title;
    let button;
    let icon;

    if (message) {
      title = <>{message}</>;
      icon = <img src={checkGreenCircle} alt="check green circle" width="20" />;
      button = (
        <Link to="/dashboard" style={styles.link}>
          <Button variant="contained" color="primary">
            Go to Dashboard
          </Button>
        </Link>
      );
    } else if (error) {
      title = <>{error}</>;
      icon = <img src={crossRedCircle} alt="cross red circle" width="20" />;
      button = (
        <Link to="/" style={styles.link}>
          <Button variant="contained">Go to Home</Button>
        </Link>
      );
    }

    const content = loaded ? (
      <>
        <div className={classes.icon}>{icon}</div>
        <div className={classes.validationMessage}>
          <Typography
            style={{ marginBottom: '10px' }}
            variant="h5"
            component="h2"
          >
            {title}
          </Typography>
          {button}
        </div>
      </>
    ) : (
      <Loader
        type="TailSpin"
        color="#324157"
        height={50}
        width={50}
        timeout={10000}
      />
    );

    return (
      <div className={classes.container}>
        <Card className={classes.card}>
          <CardContent>
            {content}
          </CardContent>
        </Card>
      </div>
    );
  }
}

export default withStyles(styles)(withRouter(ValidateEmail));
